a {
  display: inline-block;
  width:100%;
  height:100%;
  color:#888;
  text-decoration:none;
 }
 a:hover{
   color:#1a1a1a;
   text-decoration:none;
 }
.card{  
  margin-bottom: 10px;
  border-radius: 2px;
  -webkit-box-shadow: 0px 1px 5px 2px rgba(0,0,0,.1);
          box-shadow: 0px 1px 5px 2px rgba(0,0,0,.1);
  overflow: hidden;
  background: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;  
}
#home{
  width:1000px;
  margin:0 auto;
}
#home #header{
  height:48px;
  padding:5px 10px;
  margin-top:10px;
}
#home .logo{
  display: inline-block;
  width:60%;
  height:30px;
  padding: 3px 10px;
  font-size: 2em;
  font-weight: 600;
  line-height: 30px;
  vertical-align: middle;
}
#home #main{
  width: 100%;
}
#home #content{
  float: left;
  width:680px;
  height: 100%;
  clear: left;
}
#home #sidebar{
  float: right;
  width:280px;
  height: 100%;
  clear: right;
}
#home #footer{
  clear: both;
  height:40px;
  padding:10px 5px;
  line-height: 30px;
  text-align: center;
}

.horizon_list>li{
  display:inline-block;
  padding:3px 10px;
  cursor: pointer;
}

.btn{
  display: inline-block;
  padding:6px 12px;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  border:1px solid transparent;
  border-radius: 4px;
  color:#fff;
  background: #337ab7;
}
.btn>a{
  color:inherit;
}


